.App {
  text-align: center;

  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  /* width: 100%; */
  max-width: 1200px;

  margin-top: 3em;
}

.App-header {
  width: 94%;
  border-radius: 10px;
  background-color: #917055;
  height: 15vh;
  padding-bottom: 2em;

  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  align-items: center;
  box-shadow: 1px 1px 10px #000000;
  z-index: 6;


}

.App-header p {
  font-size: 1.5em;
  /* margin: 2em; */
}

button {
  position: relative;
}

.titleNote {
  margin-top: 2em;
  /* margin-left: 2em; */
  /* margin-right: 2em; */

  /* border-bottom: 2px dotted rgba(0,0,0,0.5); */
  width: 130px;

  min-height: 100px;

  /* border-radius: 0 0 0 44px; */

  background-color: rgb(129, 194, 255);
  /* #ff9ec6; */

  /* background: linear-gradient(#FCFDAF, #ffff75); */

  position: relative;

  box-shadow: 5px 3px 10px #000000;


}

.titleTop {
  text-align: left;
}

.titleTop p {
  margin-left: 1.3em;
}

.navColumn {
  width: 80%;
  height: 100%;
  margin-top: 2em;
  margin-bottom: 0.5em;
  /* background-color: yellow; */

  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: center;

  /* align-content: space-between; */

  /* background-color: #FCFDAF; */

  background-color: #005543;
  
  border-radius: 20px 200px 10px 10px ;

  z-index: 4;

}


button {
  border: 2px solid black;
  border-radius: 999px;
  background-color: rgb(88, 226, 173);
  font-size: 1.2em;

  margin: 0.5em;
  padding: 0.5em;
  min-width: 200px;


  /* transition: 0.1s; */
}

button:hover {
  background-color: #00ffcc;
  /* transition: 0.1s; */
}



/*  the three rows of the flex column "navColumn"; the right column of the app header */
.userInfo {
  color: white;
  height: 35%;
}

.hotBar {
  height: 45%;
}
.infoBar {
  font-size: 1.0em;
  height: 20%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: flex-start;

  width: 100%;

  background-color: rgb(88, 226, 173);
  border-radius: 5px;
  /* border: solid 2px black; */
  /* border-width: 2px 0 2px 0; */ 


}

.infoI {
  /* font-family: sans-serif; */
  margin-top: 1px;
  margin-left: 0.4em;
  margin-right: 0.3em;
  /* margin: 0 0.2em 0 0.4em; */
  /* margin-top: 0.2em; */
  /* margin-right: 0.2em; */
  /* font-size: 1.5em; */
  width: 19.5px;
  height: 19.5px;
  text-align: center;
  background-color: rgba(46, 114, 85, 0.527);
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;

  border-radius: 100%;

  border: 2px solid black;

}

.infoI::after {
  content: "i";

}


